const UserContext = createContext<IUserState | null>(null)

function createUser(formdata: IUserData) {
	return post<IUserResult>("user/internal", formdata)
}

const useUserState = () => {
  const [ appData, setAppData ] = useState<IAppData[]>();
  const [ userInfo, setUserInfo ] = useState<any>()
  const appState = useAppContext()
  const queryClient = useQueryClient();

  const {
		mutate,
		isLoading: isCreatingUser
	}: UseMutationResult<IUserResult, AxiosError, IUserData> = useMutation<
		IUserResult,
		AxiosError,
		IUserData
	>(createUser, {
    onSuccess: (data) => {
      appState?.setAlert({
        show: true,
        message: "User added successfully",
        status: 'success'
      })
      setUserInfo(undefined)
      setAppData(queryData?.data?.sp_practice_roles);
    },
    onError: (err) => {
      appState?.setAlert({
        show: true,
        message: "User add request failed",
        status: 'error'
      })
    }
	})

  const setSelected = (id: number) => {
    const mapped = appData?.map(app => {
      if(app.id == id) {
        app.selected = !app.selected
        return app
      } else {
        return app
      }
    })
    setAppData(mapped)
  }

  const setRole: IUserState["setRole"] = (appId, roleId) => {
    const mapped = appData?.map(app => {
      if(app.id == appId) {
        // app.selected = !app.selected
        app.roles.map(role => {
          if(role.id == roleId){
            role.isSelected = true
            return role
          } else {
            role.isSelected = false
            return role
          }
        })
        return app
      } else {
        return app
      }
    })
    setAppData(mapped)
  }

  const setSubrole: IUserState["setSubrole"] = (appId, subRoleId) => {
    const mapped = appData?.map(app => {
      if(app.id == appId) {
        // app.selected = !app.selected
        const selectedRole = app.roles.find(ele => ele.isSelected)
        selectedRole?.sub_roles?.map(subRole => {
          if(subRole.id == subRoleId){
            subRole.isSelected = true
            return subRole
          } else {
            subRole.isSelected = false
            return subRole
          }
        })
        return app
      } else {
        return app
      }
    })
    setAppData(mapped)
  }

  const setGender: IUserState["setGender"] = (appId, genderId) => {
    const mapped = appData?.map(app => {
      if(app.id == appId) {
        // app.selected = !app.selected
        app.gender.map(g => {
          if(g.id == genderId){
            g.isSelected = true
            return g
          } else {
            g.isSelected = false
            return g
          }
        })
        return app
      } else {
        return app
      }
    })
    setAppData(mapped)
  }

  const addUser = () => {
    console.log("Selected Apps: 1", appData)
    let gender = null
    let type_of_user = "external"

    const selectedApps: IAppData[] = []
    appData?.forEach(app => {
      if(app.selected) {
        console.log("Selected Apps: 2", app.name)
        let selectedRole
        const practices: { id: number }[] = []
        let tempApp: any = {}

        if(app.name == "ez-login") {
          type_of_user = "internal"
        }

        const selectChildren = (obj: IPractice | IAppData) => {
          if (!obj?.practices) {
            return;
          }
          obj.practices.forEach((pract) => {
            console.log("prac: ", practices)

            if (pract.isChecked/*  && pract.isIndeterminate */) {
              practices.push({ id: pract.id })
            }
            selectChildren(pract);
          });
        };

        selectChildren(app)

        app?.gender?.forEach(g => {
          if(g.isSelected) {
            gender = g.id
          }
        })

        app.roles.forEach(role => {
          if(role.isSelected) {
            selectedRole = {
              id: role.id,
              sub_role: role?.sub_roles?.find(subRole => subRole.isSelected)?.id || null
            }
          }
        })

        console.log("practices: ", practices)

        tempApp.id = app.id
        tempApp.practices = practices
        tempApp.role = selectedRole || {
          id: null,
          sub_role: null
        }
        selectedApps.push(tempApp)

      }
    })

    const user = {
      ...userInfo,
      type_of_user,
      dr_iq_gender_id: gender,
      apps: selectedApps
    }

    console.log("Selected Apps: ", user)

    mutate(user)

  }

  const getPractices = () => {
    return get(
      "user/service-providers/practices/roles"
    );
  };

  // Queries
  const { isLoading: appDataLoading, isError, data: queryData, error } = useQuery("todos", getPractices, {
    onSuccess: (data) => {
      setAppData(data.data.sp_practice_roles);
    },
    refetchOnWindowFocus: false
  });

  return { appData, setAppData, appDataLoading, isCreatingUser, setSelected, setRole, setSubrole, setGender, addUser, setUserInfo, userInfo }
}


export const UserProvider = ({ children }: IUserContext) => {
	const appData = useUserState()
	return <UserContext.Provider value={appData}>{children}</UserContext.Provider>
}

//use this hook to consume the context
export const useUserContext = () => {
	return useContext(UserContext)
}