import * as React from "react";
import { styled } from "@mui/material/styles";
import ArrowForwardIosSharpIcon from "@mui/icons-material/ArrowForwardIosSharp";
import Typography from "@mui/material/Typography";
import {
  Checkbox,
  Collapse,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText
} from "@mui/material";
import { IPractice } from "../../context/UserContext/UserContext.types";
import Item from "./Item";

interface PanelProps {
  option: IPractice
  onCheck: (item: IPractice, arrayIndex: number[]) => void
  index: number[]
}

export default function Panel(props: PanelProps) {
  const { option, onCheck, index } = props;
  const [expanded, setExpanded] = React.useState(false);

  // const labelId = `checkbox-list-label-${option.id}`;
  const handleToggle = (e: React.SyntheticEvent) => {
    setExpanded(!expanded);
  };

  const onCheckHandler = (e: React.SyntheticEvent, item: IPractice, indexArray: number[]) => {
    onCheck(item, indexArray);
  };

  const checkedPracticesLength = option?.practices?.filter((op) => op.isChecked)
    .length || 0;

  return (
    <>
      <Item
        name={option.name}
        expandable={!!option?.practices?.length}
        expanded={expanded}
        onClick={handleToggle}
        indeterminate={
          checkedPracticesLength > 0 &&
          checkedPracticesLength !== option?.practices?.length
          // !!option.isIndeterminate
        }
        checked={!!option.isChecked}
        onCheck={(e: React.SyntheticEvent) => onCheckHandler(e, option, [...index])}
        level={index.length}
        labelId={`checklistitem-${index.join('-')}`}
      />
      {expanded && option?.practices && (
        <Collapse in={expanded} timeout="auto" /* unmountOnExit */>
          {option?.practices?.map((prac, i) => (
            <Panel key={i} index={[...index, i]} option={prac} onCheck={onCheck} />
          ))}
        </Collapse>
      )}
    </>
  );
}
