import React, { useState } from "react";
import { Box, Button, Popper, Typography, ClickAwayListener, ListItem } from "@mui/material";
import Panel from "./Panel";
import { useQuery, useMutation, useQueryClient } from "react-query";
import axios from "axios";
import { cloneDeep } from "lodash";
import { IAppData, IPractice } from "../../context/UserContext/UserContext.types";
import { useUserContext } from "../../context/UserContext/UserContext";
import S_InputBox from "../FilterInput/SharpBox";
import { KeyboardArrowDown } from "@mui/icons-material";

interface DDProps {
  // isLoading: boolean
  setApp: (app: IAppData, appIndex: number) => void
  appIndex: number
  app: IAppData
}

export default function PracticesDropdown(props: DDProps) {
  const [anchorEl, setAnchorEl] = React.useState<HTMLElement | null>(null);
  const { app, appIndex, setApp } = props;
  const appData = useUserContext()

  const handleClick = (event: React.SyntheticEvent) => {
    setAnchorEl(anchorEl ? null : event.currentTarget as HTMLElement);
  };

  const onCheck = (option: IPractice , indexArray: number[]) => {
    const deselectPrev = (obj: IPractice | IAppData) => {
      if (!obj?.prev) {
        return;
      }
      if (obj?.checkedLength && obj?.practices && obj?.checkedLength - 1 > 0) {
        obj.isIndeterminate = true;
      }
      obj.isChecked = false;
      deselectPrev(obj.prev);
    };

    const selectPrev = (obj: IPractice | IAppData) => {
      if (!obj?.prev) {
        return;
      }

      if (obj?.checkedLength && obj?.practices && obj?.checkedLength + 1 >= obj?.practices?.length) {
        obj.isChecked = true;
      }
      selectPrev(obj.prev);
    };

    const selectChildren = (obj: IPractice) => {
      if (!obj?.practices) {
        return;
      }
      obj.practices.forEach((pract) => {
        if (obj.isChecked) {
          pract.isChecked = true;
        } else {
          pract.isChecked = false;
        }
        selectChildren(pract);
      });
    };

    // const tempLocalData = JSON.parse(JSON.stringify(localData));
    const tempLocalData = cloneDeep(app);

    let temp: IAppData | IPractice = tempLocalData;

    indexArray.forEach((ele, index) => {
      console.log("Data: 3", temp);

      let tmp = temp;
      tmp.checkedLength = tmp?.practices?.filter(
        (practice) => practice.isChecked
      )?.length || 0;
      temp = temp!.practices![ele];
      temp.prev = tmp;

      if (index === indexArray.length - 1) {
        if (!temp.isChecked) {
          temp.isChecked = true;
          selectPrev(temp);
        } else {
          temp.isChecked = false;
          deselectPrev(temp);
        }

        if (temp.practices?.length) {
          selectChildren(temp);
        }
      }
    });

    setApp(tempLocalData, appIndex);
  };

  const id = "dropdown";
  const open = Boolean(anchorEl);

  if (appData?.appDataLoading) {
    return <Typography>Loading...</Typography>;
  }

  return (
    <>
      <S_InputBox onClick={handleClick}>
        <Box width='100%' display='flex' alignItems='center' justifyContent='space-between' p={2} pr={0.1}>
          <Typography variant='subtitle1' color={"GrayText"}>Practices</Typography>
          <KeyboardArrowDown />
        </Box>
      </S_InputBox>
      {/* <Button aria-describedby={id} onClick={handleClick}>
        Practices
      </Button> */}
        <Popper id={id} open={open} anchorEl={anchorEl}>
      <ClickAwayListener onClickAway={() => setAnchorEl(null)}>
        <Box
          maxHeight={400}
          width={anchorEl?.clientWidth}
          overflow="auto"
          sx={({ palette, shadows }) => ({
            backgroundColor: palette.background.default,
            boxShadow: shadows[2]
          })}
          bgcolor="white"
        >
          {app?.practices?.length > 0 ? app?.practices?.map((panel, index) => (
            <Panel
              key={index}
              index={[index]}
              option={panel}
              onCheck={onCheck}
            />
          )) : <ListItem><Typography variant="body1">No Options...</Typography></ListItem>}
        </Box>
      </ClickAwayListener>

        </Popper>
    </>
  );
}
