import React, { PropsWithChildren, useEffect, useMemo } from 'react';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import {StyledPaper, StyledTableCell, StyledTableRow, StyledTableBody, StyledTable, StyledHeaderRow, PaginationContainer, StyledPagination, StyledPaginationItem, StyledSortIcon} from './Table.styles'
import { ITable } from './Table.types';
import { Box } from '@mui/system';
import { Typography } from '@mui/material';
import { ArrowForward, ArrowBack, SyncAlt, ExpandLess, ExpandMore } from '@mui/icons-material';
import { useTable, useFilters, usePagination, useSortBy } from 'react-table'
import typedMemo from '../../utils/typedMemo';

function Table<DataType extends object = {}>(props: PropsWithChildren<ITable<DataType>>) {
  const { columns, data, filters, rowsPerPage, useSorting, hiddenColumns } = props
  const memoizedColumns = useMemo(() => columns, [])
  const memoizedData = useMemo(() => data, [])

  const plugins: any[] = [useFilters]
  if(useSorting) {
    plugins.push(useSortBy)
  }
  plugins.push(usePagination)

  const tableInstance = useTable<DataType>({
    columns: memoizedColumns,
    data: memoizedData,
    initialState: {
      pageSize: rowsPerPage || 10,
      hiddenColumns
    }
  }, ...plugins)
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
    setFilter,
    page,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    canPreviousPage,
    canNextPage,
    pageOptions,
    state: { pageIndex, pageSize },
  } = tableInstance

  useEffect(() => {
    Object.entries(filters).forEach(([key, value]) => setFilter(key, value))
  }, [filters])

  useEffect(() => {
    if(rowsPerPage) {
      setPageSize(rowsPerPage)
    }
  }, [rowsPerPage])

  const emptyRows = Math.abs(pageSize - page.length)
  const startRowNo = (pageIndex * pageSize) + 1
  const endRowNo = (pageIndex * pageSize) + pageSize

  console.log("TABLE RERENDERED")
  
  return (
    <Box>
      <TableContainer component={StyledPaper}>
        <StyledTable sx={{ minWidth: 700 }} aria-label="customized table" {...getTableProps()}>
          <TableHead>
            {// Loop over the header rows
              headerGroups.map(headerGroup => (
                // Apply the header row props
                <StyledHeaderRow {...headerGroup.getHeaderGroupProps()}>
                  {// Loop over the headers in each row
                  headerGroup.headers.map(column => (
                    // Apply the header cell props
                    <StyledTableCell {...column.getHeaderProps(useSorting ? column.getSortByToggleProps() : undefined)}>
                      <Box display='flex' width='100%' justifyContent='space-between' alignItems='center'>
                        <>
                          {// Render the header
                          column.render('Header')}
                          { useSorting && (
                            <Box component='span'>
                              {column.isSorted
                                ? column.isSortedDesc
                                  ? <ExpandLess />
                                  : <ExpandMore />
                                : column.canSort ?
                                  <StyledSortIcon /> :
                                  null}
                              
                            </Box>
                          )}
                        </>
                      </Box>
                    </StyledTableCell>
                  ))}
                </StyledHeaderRow>
              ))}
          </TableHead>
          <StyledTableBody {...getTableBodyProps()}>
            {// Loop over the table rows
              page.map(row => {
                // Prepare the row for display
                prepareRow(row)
                return (
                  // Apply the row props
                  <StyledTableRow {...row.getRowProps()}>
                    {// Loop over the rows cells
                    row.cells.map(cell => {
                      // Apply the cell props
                      return (
                        <StyledTableCell {...cell.getCellProps()}>
                          <Typography><>{cell.render('Cell')}</></Typography>
                        </StyledTableCell>
                      )
                    })}
                  </StyledTableRow>
                )
              })}
              { new Array(emptyRows).fill(0).map(() => (
                <StyledTableRow sx={() => ({ backgroundColor: 'transparent' })}>
                  <StyledTableCell>
                    <Typography>&nbsp;</Typography>
                  </StyledTableCell>
                </StyledTableRow>
              ))}
          </StyledTableBody>
        </StyledTable>
        <PaginationContainer>
          <Box>
            <Typography>Showing {startRowNo} to {endRowNo <= rows.length ? endRowNo : rows.length} of {rows.length} entries</Typography>
          </Box>
          <Box>
            <StyledPagination
              color='primary'
              count={pageCount}
              shape="rounded"
              onChange={(e, page) => {gotoPage(page - 1)}}
              renderItem={item => (
                <StyledPaginationItem
                  {...item}
                  components={{ next: ArrowForward , previous: ArrowBack }} />
              )} />
          </Box>
        </PaginationContainer>
      </TableContainer>
    </Box>
  );
}

Table.defaultProps = {
  hiddenColumns: []
}

export default typedMemo(Table)
