import { BorderBottom } from "@mui/icons-material"
import {
	AppBar,
	Avatar,
	Box,
	Breadcrumbs,
	Container,
	Divider,
	Grid,
	IconButton,
	Link as MuiLink,
	Menu,
	MenuItem,
	Paper,
	Toolbar,
	Tooltip,
	Typography
} from "@mui/material"
import React from "react"
import { ReactComponent as EZLOGO } from "../../assets/svgs/EZLOGO.svg"
import AppsIcon from "@mui/icons-material/Apps"
import { SBox, SHeaderAppBar, SHeaderToolbar, SMenu } from "./index.style"
import { ExpandMoreIcon } from "../../utils/Icons"
import { stringAvatar } from "../../utils/AvatarUtill"
import getBreadCrumbs from "../../utils/getBreadCrumbs"
import { useLocation, Link, useNavigate } from "react-router-dom"
import { useAuthContext } from "../../context/AuthContext/AuthContext"

const pages = ["Products", "Pricing", "Blog"]
const settings = ["Profile", "Account", "Dashboard", "Logout"]

const AppNavbar = () => {
	const [anchorElNav, setAnchorElNav] = React.useState(null)
	const [anchorElUser, setAnchorElUser] = React.useState(null)
	const location = useLocation()
	const navigate = useNavigate()
	const auth = useAuthContext()

	const handleOpenNavMenu = (event: any) => {
		setAnchorElNav(event.currentTarget)
	}
	const handleOpenUserMenu = (event: any) => {
		setAnchorElUser(event.currentTarget)
	}

	const handleCloseNavMenu = () => {
		setAnchorElNav(null)
	}

	const handleCloseUserMenu = () => {
		setAnchorElUser(null)
	}

	const handleUserMenuClick = async (setting: string) => {
		switch (setting) {
			case "Profile": {
				navigate("/profile")
				break
			}

			case "Account": {
				break
			}
			case "Dashboard": {
				navigate("dashboard")
				break
			}
			case "Logout": {
				if (auth) {
					const result = await auth.logOut()
					console.log("result", result)
				}
				break
			}
		}
	}
	return (
		<Grid item>
			<SHeaderAppBar position="static" color="inherit" elevation={0}>
				<SHeaderToolbar disableGutters>
					<EZLOGO width={180} height={42} />
					<SBox display="flex">
						<AppsIcon fontSize="large" sx={{ color: "#454545" }} />
						<Avatar
							{...stringAvatar("Fahad Sohail")}
							alt="Remy Sharp"
							src="/static/images/avatar/2.jpg"
						/>

						<Tooltip title="Open settings">
							<IconButton
								disableRipple
								onClick={handleOpenUserMenu}
								sx={{ p: 0 }}
							>
								<ExpandMoreIcon />
							</IconButton>
						</Tooltip>
						<SMenu
							sx={{ mt: "45px" }}
							id="menu-appbar"
							anchorEl={anchorElUser}
							anchorOrigin={{
								vertical: "top",
								horizontal: "right"
							}}
							keepMounted
							transformOrigin={{
								vertical: "top",
								horizontal: "right"
							}}
							open={Boolean(anchorElUser)}
							onClose={handleCloseUserMenu}
						>
							{settings.map(setting => (
								<MenuItem
									divider
									key={setting}
									onClick={() => handleUserMenuClick(setting)}
								>
									<Typography textAlign="center">{setting}</Typography>
								</MenuItem>
							))}
						</SMenu>
					</SBox>
				</SHeaderToolbar>
			</SHeaderAppBar>
			<Container maxWidth="xl">
				<Box my={1} pl={2.5}>
					<Breadcrumbs aria-label="breadcrumb">
						{getBreadCrumbs(location).map(crumb => {
							if (crumb.link) {
								return (
									<MuiLink
										component={Link}
										to={crumb.link}
										underline="hover"
										color="primary"
									>
										{crumb.label}
									</MuiLink>
								)
							} else {
								return (
									<Typography color="text.primary">{crumb.label}</Typography>
								)
							}
						})}
					</Breadcrumbs>
				</Box>
			</Container>
		</Grid>
	)
}

export default AppNavbar
