import React, { useEffect, useState } from 'react'
import { Box, IconButton } from '@mui/material'
import IconContainer from '../IconContainer'
import Select from "../../components/FilterInput/FilterSelect"
import { useFieldArray, useFormContext } from 'react-hook-form'
import { IForm, IProduct } from '../../pages/AddUser/AddUser.types'
import { AddCircle, Delete, RemoveCircle } from '@mui/icons-material'
import { IAppData } from '../../context/UserContext/UserContext.types'
import Panel from './Panel'
import PracticesDropdown from './PracticesDropdown'
import { useUserContext } from '../../context/UserContext/UserContext'
import axios from 'axios'

type Props = {
  index: number
  Logo: /* React.ReactNode */string
  selected: boolean | undefined
  app: IAppData
}

const ProductList = (props: Props) => {
  const [logo, setLogo] = useState()

  const { index, Logo, selected, app } = props
  const { control, getValues, setValue, register } = useFormContext()
  const appData = useUserContext()

  const setApp = (data: IAppData, appIndex: number) => {
    const appId = data.id;
    if(appData?.appData) {
      appData?.setAppData([
        ...appData?.appData?.map((t_app, index) => {
          if (index == appIndex) {
            return data;
          } else {
            return t_app;
          }
        })
      ]);
    }
  };

  useEffect(() => {
    register('apps')
  }, [register])

  const allApps: IForm = getValues("apps")
  const selectedRole = app.roles.find(role => role.isSelected)
  
  return (
    <Box display="flex" width="100%" gap={2} p={1} sx={({ palette }) => ({ backgroundColor: palette.secondary[200] })}>
      <Box onClick={() => {
        appData?.setSelected(app.id)
        // if(allApps.find(ele => ele.id == app.id)) {

        // }

        // setValue("apps", [...getValues("apps"), { id: app.id }])
      }}
      sx={{ width: "20%", height: '100%'}}
    >
        <IconContainer isActive={selected}>
          <Box sx={{maxWidth:"100%", maxHeight: "100%", height: "100%"}}>
            <img src={Logo} style={{ width: '100%', height: '100%' }} draggable={false}/>
          </Box>
        </IconContainer>
      </Box>
      <Box sx={{ display: 'flex', flexDirection: 'column', width: "80%" }}>
        <Box pb={1} sx={{ display: 'flex', gap: 2, width: "100%" }}>
          <Box sx={{ width: "65%" }}>
            <PracticesDropdown app={app} appIndex={index} setApp={setApp} />
          </Box>
          <Box sx={{ width: "35%" }}>
            <Select
              value={selectedRole?.id?.toString() || ""}
              placeholder="Select Role"
              onChange={(value) => {
                appData?.setSubrole(app.id, null)
                if(value) {
                  appData?.setRole(app.id, parseInt(value))
                } else {
                  appData?.setRole(app.id, null)
                }
                // console.log("Select", value)
              }}
            >
              {
                app.roles.map(role => {
                  return (
                    <option label={role.name} value={role.id}>
                      {role.name}
                    </option>
                  )
                })
              }
            </Select>
          </Box>
        </Box>
        {app.name == 'dr-iq' &&
          <Box sx={{ display: 'flex', gap: 2, width: "100%" }}>
            <Box sx={{ width: "50%" }}>
              <Select
                value={app.gender.find(g => g.isSelected)?.id?.toString() || ""}
                placeholder="Select Gender"
                onChange={(value) => {
                  if(value) {
                    appData?.setGender(app.id, parseInt(value))
                  } else {
                    appData?.setGender(app.id, null)
                  }
                }}
              >
                {
                  app.gender.map(gen => {
                    return (
                      <option label={gen.name} value={gen.id}>
                        {gen.name}
                      </option>
                    )
                  })
                }
              </Select>
            </Box>
            <Box sx={{ width: "50%" }}>
              <Select
                disabled={!selectedRole?.id}
                value={selectedRole?.sub_roles?.find(ele => ele.isSelected)?.id?.toString() || ""}
                onChange={(value) => {
                  if(value) {
                    appData?.setSubrole(app.id, parseInt(value))
                  } else {
                    appData?.setSubrole(app.id, null)
                  }
                }}
              >
                {/*@ts-ignore*/}
                {
                  selectedRole ? selectedRole?.sub_roles?.map(role => {
                    return (
                      <option label={role.name} value={role.id}>
                        {role.name}
                      </option>
                    )
                  }) : null
                  // <option label={""} value={""}>
                  //       {}
                  //     </option>
                }
              </Select>
            </Box>
          </Box>
        }
      </Box>
    </Box>
  )
}

export default ProductList