			// 	{ key: "ez-login", icon: Ezlogo, selected: true, products: [{practice: 'surgeon', role: 'admin'}, {practice: 'surgeon', role: 'admin'}] },
			// 	{ key: "dr-iq", icon: DriqText, selected: false, products: [] },
			// 	{ key: "ez-web", icon: EzwebText, selected: false, products: [] },
